.user {
    width: 100%;
    min-height: 100vh;
    padding: 40px;

    display: flex;
    align-items: center;

    &__wrapper {
        width: 100%;
        margin: 0 auto;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}


@media (max-width: 767px) {
    .user {
        padding: 20px 15px;
    }
}