.author {
    &__tab {
        display: none;

        &--active {
            display: block;
        }
    }

    &__content {
        max-width: 675px;
        padding-left: 95px;

        & h2,
        & h3,
        & h4,
        & h5,
        & h6 {
            color: var(--grey-1);
            margin: revert;
        }

        & p {
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--grey-1);
            margin: revert;
        }

        & ul,
        & ol {
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--grey-1);
            margin: revert;
            padding: revert;
        }

        & li {
            list-style: revert;
        }
    }
}

@media (max-width: 1023px) {
    .author {
        &__content {
            padding-left: 60px;
            padding-right: 60px;
        }
    }
}


@media (max-width: 767px) {
    .author {
        &__content {
            padding: 0;

            & p,
            & ul,
            & ol {
                font-size: 14px;
            }
        }
    }
}